import {
  isDropboxConfigured,
  isDropboxAuthed,
  handleDropboxRedirect,
  beginDropboxAuth,
  signOutDropbox,
  loadEventsFromDropbox,
  saveEventsToDropbox,
  loadMetricsFromDropbox,
  saveMetricsToDropbox
} from './dropbox';
import { loadData, saveData } from '../data';
import { computeMetricsFromEvents } from '../utils';

export async function initStorageAuth() {
  if (!isDropboxConfigured()) return { provider: 'local' };
  const authedNow = await handleDropboxRedirect(); // picks up token on first redirect
  return { provider: isDropboxAuthed() ? 'dropbox' : 'local', authedNow };
}

export function canUseCloud() {
  return isDropboxConfigured();
}
export function isCloudSignedIn() {
  return isDropboxAuthed();
}
export function signInCloud() {
  return beginDropboxAuth();
}
export function signOutCloud() {
  return signOutDropbox();
}

export async function loadEvents() {
  if (isDropboxConfigured() && isDropboxAuthed()) {
    return await loadEventsFromDropbox();
  }
  return loadData();
}
export async function saveEvents(eventsObj) {
  if (isDropboxConfigured() && isDropboxAuthed()) {
    await saveEventsToDropbox(eventsObj);
    // also recompute + save metrics in cloud
    const metrics = computeMetricsFromEvents(eventsObj);
    await saveMetricsToDropbox(metrics);
    return 'dropbox';
  }
  // local fallback
  saveData(eventsObj);
  const metrics = computeMetricsFromEvents(eventsObj);
  localStorage.setItem('journalMetrics', JSON.stringify(metrics));
  return 'local';
}

export async function loadMetrics() {
  if (isDropboxConfigured() && isDropboxAuthed()) {
    return await loadMetricsFromDropbox();
  }
  try {
    return JSON.parse(localStorage.getItem('journalMetrics') || '{}');
  } catch {
    return {};
  }
}
