// src/utils.js

// Recompute daily metrics from events-by-date
// eventsByDate: { [date]: [ { rating:number, categories:string[] } ] }

export function computeMetricsFromEvents(eventsByDate) {
  const out = {};
  for (const [date, arr] of Object.entries(eventsByDate || {})) {
    const catCounts = {};
    let total = 0, n = 0;
    (arr || []).forEach(ev => {
      const r = Number(ev.rating || 0);
      if (r) { total += r; n++; }
      (ev.categories || []).forEach(cid => {
        catCounts[cid] = (catCounts[cid] || 0) + 1;
      });
    });
    out[date] = { catCounts, avgRating: n ? Math.round((total/n)*10)/10 : 0 };
  }
  return out;
}
