// src/storage/dropbox.js
import { Dropbox } from 'dropbox';

const APP_KEY = process.env.REACT_APP_DROPBOX_APP_KEY;
const TOKEN_KEY = 'mm_dropbox_token';
const EVENTS_PATH = '/events.json';
const METRICS_PATH = '/metrics.json';

function getToken() { return localStorage.getItem(TOKEN_KEY) || null; }
function setToken(t) { if (t) localStorage.setItem(TOKEN_KEY, t); }
export function signOutDropbox() { localStorage.removeItem(TOKEN_KEY); }
export function isDropboxConfigured() { return Boolean(APP_KEY); }
export function isDropboxAuthed() { return Boolean(getToken()); }

function getDbx() {
  const accessToken = getToken();
  return new Dropbox({ accessToken, clientId: APP_KEY, fetch: window.fetch.bind(window) });
}

/** Build the OAuth URL (implicit grant) and redirect */
export async function beginDropboxAuth() {
  // implicit grant = token in hash
  const redirectUri = window.location.origin + window.location.pathname;
  // Scope string can be omitted if your app has the scopes preset
  const authUrl =
    `https://www.dropbox.com/oauth2/authorize?response_type=token` +
    `&client_id=${encodeURIComponent(APP_KEY)}` +
    `&redirect_uri=${encodeURIComponent(redirectUri)}`;
  window.location.assign(authUrl);
}

/** On load, check location.hash for access_token and store it */
export async function handleDropboxRedirect() {
  if (!isDropboxConfigured()) return false;
  if (!window.location.hash) return false;

  const hash = window.location.hash.startsWith('#') ? window.location.hash.slice(1) : window.location.hash;
  const params = new URLSearchParams(hash);
  const token = params.get('access_token');
  if (token) {
    setToken(token);
    // Clean the URL (remove hash)
    window.history.replaceState({}, document.title, window.location.pathname);
    return true;
  }
  return false;
}

async function readJSON(path) {
  const dbx = getDbx();
  try {
    const { result } = await dbx.filesDownload({ path });
    const blob = result.fileBlob || result;
    const text = await blob.text();
    return JSON.parse(text);
  } catch (e) {
    // file probably doesn't exist yet → return empty
    return {};
  }
}

async function writeJSON(path, obj) {
  const dbx = getDbx();
  const contents = new Blob([JSON.stringify(obj, null, 2)], { type: 'application/json' });
  await dbx.filesUpload({ path, contents, mode: { '.tag': 'overwrite' } });
}

export async function loadEventsFromDropbox() { return readJSON(EVENTS_PATH); }
export async function saveEventsToDropbox(eventsObj) { await writeJSON(EVENTS_PATH, eventsObj); }
export async function loadMetricsFromDropbox() { return readJSON(METRICS_PATH); }
export async function saveMetricsToDropbox(metricsObj) { await writeJSON(METRICS_PATH, metricsObj); }
