
import React, { useState, useEffect } from 'react';
import { defaultCategories } from './data';
import { initStorageAuth, loadMetrics } from './storage/router';

function getDatesInRange(start, end) {
  const dates = [];
  const curr = new Date(start);
  const last = new Date(end);
  while (curr <= last) {
    dates.push(curr.toISOString().slice(0,10));
    curr.setDate(curr.getDate() + 1);
  }
  return dates;
}

export default function Dashboard() {
  const [metrics, setMetrics] = useState({});
  useEffect(() => {
    (async () => {
      await initStorageAuth();
      const m = await loadMetrics();
      setMetrics(m || {});
    })();
  }, []);

  const today = new Date();
  const defaultEnd = today.toISOString().slice(0,10);
  const defaultStart = new Date(today.getTime() - 6*24*60*60*1000).toISOString().slice(0,10);
  const [startDate, setStartDate] = useState(defaultStart);
  const [endDate, setEndDate] = useState(defaultEnd);

  const dates = getDatesInRange(startDate, endDate);

  // Aggregate category counts in range
  const catCounts = {};
  dates.forEach(d => {
    const c = metrics[d]?.catCounts || {};
    Object.entries(c).forEach(([cid, cnt]) => {
      catCounts[cid] = (catCounts[cid] || 0) + cnt;
    });
  });

  return (
    <div>
      <h3>Dashboard</h3>
      <div style={{ marginBottom: 16 }}>
        <label>From </label>
        <input type="date" value={startDate} onChange={e => setStartDate(e.target.value)} />
        <label style={{ marginLeft: 12 }}>To </label>
        <input type="date" value={endDate} onChange={e => setEndDate(e.target.value)} />
      </div>

      <div style={{ display:'flex', gap: 32, flexWrap:'wrap' }}>
        <div>
          <b>Category Distribution</b>
          <div>
            {defaultCategories.map(cat => (
              <div key={cat.id}>{cat.icon} {cat.name}: {catCounts[cat.id] || 0}</div>
            ))}
          </div>
        </div>
        <div>
          <b>Mood Timeline (Avg Rating)</b>
          <div>
            {dates.map(d => (
              <div key={d}>{d}: {(metrics[d]?.avgRating ?? 0)}★</div>
            ))}
          </div>
        </div>
      </div>

      <div style={{ marginTop: 24, overflowX:'auto' }}>
        <table style={{ minWidth: 800 }}>
          <thead>
            <tr>
              <th>Total</th>
              {defaultCategories.map(cat => <th key={cat.id}>{cat.icon}</th>)}
            </tr>
            <tr>
              <th>Category</th>
              {defaultCategories.map(cat => <th key={cat.id}>{cat.name}</th>)}
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>Totals</td>
              {defaultCategories.map(cat => (
                <td key={cat.id}>{catCounts[cat.id] || 0}</td>
              ))}
            </tr>
            {dates.map(d => (
              <tr key={d} style={{ background:'#fafafa' }}>
                <td>{d}</td>
                {defaultCategories.map(cat => (
                  <td key={cat.id}>{metrics[d]?.catCounts?.[cat.id] || 0}</td>
                ))}
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}
