
import React, { useEffect, useState } from 'react';
import { defaultCategories, defaultPurposes } from './data';
import { initStorageAuth, loadEvents, saveEvents } from './storage/router';
import { computeMetricsFromEvents } from './utils';

/* --- PM marker insertion for 12-hour logs --- */
function insertPMMarker(rawText) {
  const lines = rawText.split('\n');
  let result = [];
  let found12 = false;
  for (let line of lines) {
    const t = line.trim();
    if (/^\d{2}\/\d{2}\/\d{4}$/.test(t)) { found12 = false; result.push(line); }
    else if (!found12 && /^12(?:[-–]\d{1,2})?$/.test(t)) { found12 = true; result.push(line, '---PM---'); }
    else { result.push(line); }
  }
  return result.join('\n');
}

/* --- Parse your text log format into { date: events[] } --- */
function importFromLogFormat(text) {
  const dateLine = /^(\d{2})\/(\d{2})\/(\d{4})$/;
  const hourLine = /^(\d{1,2})(?:[-–](\d{1,2}))?$/;
  const pmMarker = /^---\s*PM\s*---$/i;

  let currentDate = null, currentHour = null, isPM = false;
  const out = {};
  let imported = 0;

  for (const raw of text.split('\n')) {
    const line = raw.trim();
    if (!line) continue;
    if (pmMarker.test(line)) { isPM = true; continue; }
    let m;
    if ((m = dateLine.exec(line))) {
      const [_, mm, dd, yyyy] = m;
      currentDate = `${yyyy}-${mm.padStart(2,'0')}-${dd.padStart(2,'0')}`;
      out[currentDate] = out[currentDate] || [];
      isPM = false;
    } else if ((m = hourLine.exec(line))) {
      let h = parseInt(m[1], 10);
      if (isPM && h !== 12) h += 12;
      currentHour = h;
    } else if (currentDate && currentHour != null) {
      let note = line;
      const purposes = [];
      if (/\bgrateful\b/i.test(note)) purposes.push('meaning');
      if (/\bhappy\b/i.test(note)) purposes.push('happy');
      if (/adventure/i.test(note)) purposes.push('adventure');

      const categories = [];
      if (/\b(learn|class)\b/i.test(note)) categories.push('learn');
      if (/\bwork\b/i.test(note)) categories.push('work');
      if (/\b(run|bike|gym)\b/i.test(note)) categories.push('exercise');
      if (/\b(meditation|stretch|chill|nap)\b/i.test(note)) categories.push('mindful');
      if (/\bego\b/i.test(note)) categories.push('ego');
      if (/\bgrateful\b/i.test(note)) categories.push('grateful');
      if (/\berrand\b/i.test(note)) categories.push('errand');
      if (/\bcreate\b/i.test(note)) categories.push('create');
      if (/\b(career|job)\b/i.test(note)) categories.push('career');
      if (/\b(kids|nana|casper|ray)\b/i.test(note)) categories.push('kids');
      if (/\bbow\b/i.test(note)) categories.push('bow');
      if (/\bparent\b/i.test(note)) categories.push('parent');
      if (/\bfriend\b/i.test(note)) categories.push('friend');
      if (/\b(volunteer|scout)\b/i.test(note)) categories.push('volunteer');
      if (/\b(drive|commute)\b/i.test(note)) categories.push('commute');
      if (/\b(library|read)\b/i.test(note)) categories.push('read');
      if (/\b(tv|phone)\b/i.test(note)) categories.push('media');
      if (/\b(money|bookkeep)\b/i.test(note)) categories.push('money');
      if (/\b(lunch|breakfast|dinner|meal|juice)\b/i.test(note)) categories.push('meal');
      if (/\b(woke|sleep)\b/i.test(note)) categories.push('sleep');

      let rating = 3;
      if (note.includes('+') || /grateful|happy|good/i.test(note)) rating = 5;
      else if (/regret|ego|bad|hurt/i.test(note)) rating = 2;
      if (/\s\+$/.test(note)) { rating = Math.min(5, rating+1); note = note.replace(/\s\+$/, '').trim(); }
      if (/\s-$/.test(note)) { rating = Math.max(1, rating-1); note = note.replace(/\s-$/, '').trim(); }

      out[currentDate].push({
        id: Math.random().toString(36).slice(2),
        hour: currentHour,
        note,
        rating,
        categories,
        purposes,
        context: {}
      });
      imported++;
    }
  }
  if (!imported) throw new Error('No events found in text.');
  return out;
}

function mergeEvents(existing, incoming) {
  const merged = { ...existing };
  Object.keys(incoming || {}).forEach(d => {
    const inc = incoming[d] || [];
    const ex = existing[d] || [];
    const out = [...ex];
    inc.forEach(ev => {
      const dup = ex.some(e => e.hour === ev.hour && e.note === ev.note);
      if (!dup) out.push(ev);
    });
    merged[d] = out;
  });
  return merged;
}

export default function Settings() {
  const [provider, setProvider] = useState('local');
  const [importText, setImportText] = useState('');
  const [msg, setMsg] = useState('');

  useEffect(() => {
    (async () => {
      const info = await initStorageAuth();
      setProvider(info.provider);
    })();
  }, []);

  async function importFromText() {
    try {
      const pre = insertPMMarker(importText);
      const parsed = importFromLogFormat(pre);
      const current = await loadEvents();
      const merged = mergeEvents(current, parsed);
      await saveEvents(merged);
      setMsg('Text import saved.');
    } catch (e) {
      setMsg('Import failed: ' + e.message);
    }
  }

  function exportJSON() {
    (async () => {
      const events = await loadEvents();
      const blob = new Blob([JSON.stringify(events, null, 2)], { type:'application/json' });
      const url = URL.createObjectURL(blob);
      const a = document.createElement('a');
      a.href = url; a.download = 'journal-export.json'; a.click();
      URL.revokeObjectURL(url);
    })();
  }

  function exportMetricsCSV() {
    (async () => {
      const events = await loadEvents();
      const m = computeMetricsFromEvents(events);
      const cols = ['work','learn','play','family','grateful','ego','connect','exercise','errand','create','career','class','kids','bow','parent','friend','volunteer','commute','mindful','meal','media','money','read','sleep'];
      const dates = Object.keys(m).sort();
      const rows = [['date', ...cols, 'avgRating']];
      dates.forEach(d => {
        const cc = m[d].catCounts || {};
        rows.push([d, ...cols.map(cid => cc[cid] || 0), m[d].avgRating || 0]);
      });
      const csv = rows.map(r => r.join(',')).join('\n');
      const blob = new Blob([csv], { type:'text/csv' });
      const url = URL.createObjectURL(blob);
      const a = document.createElement('a');
      a.href = url; a.download = 'daily-metrics.csv'; a.click();
      URL.revokeObjectURL(url);
    })();
  }

  // JSON import
  function handleJSONImport(e) {
    const file = e.target.files[0];
    if (!file) return;
    const reader = new FileReader();
    reader.onload = async evt => {
      try {
        const json = JSON.parse(evt.target.result);
        if (typeof json !== 'object' || Array.isArray(json)) throw new Error('Invalid JSON shape');
        const cur = await loadEvents();
        const merged = mergeEvents(cur, json);
        await saveEvents(merged);
        setMsg('JSON import saved.');
      } catch (err) {
        setMsg('JSON import failed: ' + err.message);
      }
    };
    reader.readAsText(file);
  }

  // CSV import (date,hour,note,rating,categories,purposes,weather,location,movement)
  function parseCsvLine(line) {
    const m = line.match(/("([^"]|"")*"|[^,]*)(?=,|$)/g) || [];
    return m.map(cell => {
      const c = cell.trim();
      return c.startsWith('"') && c.endsWith('"') ? c.slice(1, -1).replace(/""/g, '"') : c;
    });
  }
  function handleCSVImport(e) {
    const file = e.target.files[0];
    if (!file) return;
    const reader = new FileReader();
    reader.onload = async evt => {
      try {
        const text = evt.target.result;
        const lines = text.split(/\r?\n/).filter(Boolean);
        if (lines.length < 2) throw new Error('CSV missing rows');
        const header = parseCsvLine(lines[0]);
        const idx = header.reduce((m, col, i) => { m[col.trim().toLowerCase()] = i; return m; }, {});
        if (idx.date == null || idx.hour == null || idx.note == null) throw new Error('CSV missing date/hour/note');
        const cur = await loadEvents();
        let added = 0;
        lines.slice(1).forEach(line => {
          const cols = parseCsvLine(line);
          const date = cols[idx.date];
          if (!date) return;
          const ev = {
            id: Math.random().toString(36).slice(2),
            hour: parseInt(cols[idx.hour],10) || 0,
            note: cols[idx.note] || '',
            rating: idx.rating!=null ? (parseInt(cols[idx.rating],10)||3) : 3,
            categories: idx.categories!=null ? (cols[idx.categories]||'').split('|').filter(Boolean) : [],
            purposes: idx.purposes!=null ? (cols[idx.purposes]||'').split('|').filter(Boolean) : [],
            context: {
              weather: idx.weather!=null ? (cols[idx.weather]||'') : '',
              location: idx.location!=null ? (cols[idx.location]||'') : '',
              movement: idx.movement!=null ? (cols[idx.movement]||'') : ''
            }
          };
          cur[date] = cur[date] || [];
          if (!cur[date].some(e => e.hour===ev.hour && e.note===ev.note)) {
            cur[date].push(ev); added++;
          }
        });
        await saveEvents(cur);
        setMsg(`CSV import saved. Added ${added} events.`);
      } catch (err) {
        setMsg('CSV import failed: ' + err.message);
      }
    };
    reader.readAsText(file);
  }

  return (
    <div>
      <h3>Settings</h3>
      <div style={{ marginBottom: 12 }}><b>Storage:</b> {provider === 'dropbox' ? 'Dropbox App Folder' : 'Local only (browser)'} </div>

      <section>
        <h4>Categories</h4>
        <div style={{ display:'flex', gap:12, flexWrap:'wrap' }}>
          {defaultCategories.map(cat => (
            <div key={cat.id} style={{ display:'flex', gap:4, alignItems:'center' }}>
              <span>{cat.icon}</span><span>{cat.name}</span>
            </div>
          ))}
        </div>
        <h4>Purposes</h4>
        <div style={{ display:'flex', gap:12, flexWrap:'wrap' }}>
          {defaultPurposes.map(pur => (
            <div key={pur.id} style={{ display:'flex', gap:4, alignItems:'center' }}>
              <span>{pur.icon}</span><span>{pur.name}</span>
            </div>
          ))}
        </div>
      </section>

      <section style={{ marginTop:24 }}>
        <h4>Import from Text</h4>
        <textarea rows={6} cols={60} value={importText} onChange={e => setImportText(e.target.value)} placeholder="Paste your log text here" />
        <div><button onClick={importFromText}>Import Text</button></div>
      </section>

      <section style={{ marginTop:24 }}>
        <h4>Import from JSON</h4>
        <input type="file" accept="application/json" onChange={handleJSONImport} />
      </section>

      <section style={{ marginTop:24 }}>
        <h4>Import from CSV</h4>
        <input type="file" accept=".csv,text/csv" onChange={handleCSVImport} />
      </section>

      <section style={{ marginTop:24 }}>
        <button onClick={exportJSON}>Export Events as JSON</button>
        <button style={{ marginLeft:8 }} onClick={exportMetricsCSV}>Export Daily Metrics CSV</button>
      </section>

      <div style={{ marginTop:8, color: msg.startsWith('Import failed') || msg.includes('failed') ? 'red' : 'green' }}>{msg}</div>
    </div>
  );
}
