
import React, { useState, useEffect } from 'react';
import { defaultCategories, defaultPurposes } from './data';
import { initStorageAuth, loadEvents, saveEvents } from './storage/router';

/* Auto-tag keywords */
const categoryKeywords = {
  work: ['work', 'job', 'task', 'admin'],
  learn: ['learn', 'study', 'practice'],
  play: ['play', 'fun', 'game'],
  family: ['family', 'parents', 'mom', 'dad'],
  grateful: ['grateful', 'thank'],
  ego: ['ego', 'regret', 'criticism'],
  connect: ['connect', 'help', 'support', 'value'],
  exercise: ['run', 'bike', 'gym', 'exercise', 'walk', 'hike'],
  errand: ['errand', 'shop', 'buy', 'purchase'],
  create: ['create', 'make', 'build', 'write'],
  career: ['career', 'role', 'promotion'],
  class: ['class', 'course', 'lesson'],
  kids: ['kid', 'kids', 'child', 'children', 'nana', 'casper', 'ray'],
  bow: ['bow', 'bowen'],
  parent: ['parent', 'parents'],
  friend: ['friend', 'friends', 'buddy', 'pal'],
  volunteer: ['volunteer', 'service', 'scout'],
  commute: ['commute', 'drive', 'bus', 'train'],
  mindful: ['mindful', 'meditate', 'meditation', 'chill', 'nap'],
  meal: ['meal', 'eat', 'dinner', 'lunch', 'breakfast', 'juice'],
  media: ['media', 'tv', 'video', 'podcast'],
  money: ['money', 'finance', 'budget', 'pay', 'bookkeep'],
  read: ['read', 'book', 'article', 'library'],
  sleep: ['woke', 'sleep', 'nap', 'rest']
};

export default function DayView() {
  const [data, setData] = useState({});
  const [date, setDate] = useState(() => new Date().toISOString().slice(0, 10));
  const [showEditor, setShowEditor] = useState(false);
  const [editEvent, setEditEvent] = useState(null);

  useEffect(() => {
    (async () => {
      await initStorageAuth();
      const ev = await loadEvents();
      setData(ev || {});
    })();
  }, []);

  async function persistAll(newData) {
    setData(newData);
    await saveEvents(newData);
  }

  function startEdit(hour, event = null) {
    setEditEvent(event ? { ...event } : { hour, note:'', rating:3, categories:[], purposes:[], context:{} });
    setShowEditor(true);
  }

  function autoTag(ev) {
    const text = ev.note || '';
    const tags = new Set(ev.categories || []);
    Object.entries(categoryKeywords).forEach(([catId, list]) => {
      if (tags.has(catId)) return;
      for (let kw of list) {
        const pattern = `\\b${kw.replace(/[.*+?^${}()|[\\]\\]/g, '\\$&')}\\b`;
        const re = new RegExp(pattern, 'i');
        if (re.test(text)) { tags.add(catId); break; }
      }
    });
    return Array.from(tags);
  }

  async function saveEvent(ev) {
    ev.categories = autoTag(ev);
    const day = data[date] ? [...data[date]] : [];
    if (ev.id) {
      const idx = day.findIndex(e => e.id === ev.id);
      if (idx !== -1) day[idx] = ev;
    } else {
      ev.id = Math.random().toString(36).slice(2);
      day.push(ev);
    }
    await persistAll({ ...data, [date]: day });
    setShowEditor(false);
  }

  async function deleteEvent(ev) {
    const day = (data[date] || []).filter(e => e.id !== ev.id);
    await persistAll({ ...data, [date]: day });
    setShowEditor(false);
  }

  const eventsByHour = Array.from({ length: 24 }, (_, h) => (data[date] || []).filter(ev => +ev.hour === h));

  return (
    <div>
      <div style={{ marginBottom: 8 }}>
        <input type="date" value={date} onChange={e => setDate(e.target.value)} />
        <button onClick={() => startEdit(null)}>+ Add Event</button>
      </div>
      <div>
        {eventsByHour.map((events, hour) => (
          <div key={hour} className="hour-row">
            <b style={{ width: 50, display:'inline-block' }}>{String(hour).padStart(2,'0')}:00</b>
            {events.length === 0 && (
              <button style={{ fontSize: 11, marginLeft: 6 }} onClick={() => startEdit(hour)}>+ Add</button>
            )}
            {events.map(ev => (
              <div key={ev.id} className="event-row" onClick={() => startEdit(ev.hour, ev)}>
                <span style={{ width: 36 }}>{ev.rating}★</span>
                <span className="note">{ev.note}</span>
                <span style={{ marginLeft: 'auto' }}>
                  {ev.categories.map(cid => defaultCategories.find(c => c.id === cid)?.icon)}
                  {ev.purposes && ev.purposes.map(pid => defaultPurposes.find(p => p.id === pid)?.icon)}
                </span>
              </div>
            ))}
          </div>
        ))}
      </div>

      {showEditor && (
        <EventEditor event={editEvent} onSave={saveEvent} onCancel={() => setShowEditor(false)} onDelete={deleteEvent} />
      )}
    </div>
  );
}

function EventEditor({ event, onSave, onCancel, onDelete }) {
  const [note, setNote] = useState(event.note);
  const [rating, setRating] = useState(event.rating);
  const [categories, setCategories] = useState(event.categories || []);
  const [purposes, setPurposes] = useState(event.purposes || []);
  const hour = event.hour;

  return (
    <div className="editor">
      <div style={{ display:'flex', justifyContent:'space-between' }}>
        <b>{hour != null ? `${String(hour).padStart(2,'0')}:00` : 'New'} Event</b>
        <button onClick={onCancel}>✕</button>
      </div>
      <textarea value={note} onChange={e => setNote(e.target.value)} rows={3} placeholder="Note..." style={{ width:'100%', margin:'8px 0' }} />
      <div>
        <label>Rating:&nbsp;
          <select value={rating} onChange={e => setRating(+e.target.value)}>
            {[1,2,3,4,5].map(i => <option key={i} value={i}>{i}★</option>)}
          </select>
        </label>
      </div>
      <div style={{ marginTop: 8 }}>
        <strong>Categories: </strong>
        {defaultCategories.map(cat => (
          <label key={cat.id} style={{ marginRight: 8 }}>
            <input
              type="checkbox"
              checked={categories.includes(cat.id)}
              onChange={e => setCategories(e.target.checked ? [...categories, cat.id] : categories.filter(id => id !== cat.id))}
            /> {' '}{cat.icon} {cat.name}
          </label>
        ))}
      </div>
      <div style={{ marginTop: 8 }}>
        <strong>Purposes: </strong>
        {defaultPurposes.map(pur => (
          <label key={pur.id} style={{ marginRight: 8 }}>
            <input
              type="checkbox"
              checked={purposes.includes(pur.id)}
              onChange={e => setPurposes(e.target.checked ? [...purposes, pur.id] : purposes.filter(id => id !== pur.id))}
            /> {' '}{pur.icon} {pur.name}
          </label>
        ))}
      </div>
      <div style={{ marginTop: 8 }}>
        <button onClick={() => onSave({ ...event, note, rating, categories, purposes })}>Save</button>
        {event.id && <button style={{ marginLeft: 8, color:'red' }} onClick={() => onDelete(event)}>Delete</button>}
      </div>
    </div>
  );
}
